body.body {
  font-family: 'UniversLTStd';
  color: #1B1C20;
  min-height: 100vh;
  overflow-x: hidden;
  display: flex;
  font-size: 16px;
  line-height: 1.5; }
  body.body a {
    transition: all 0.3s linear;
    text-decoration: none; }
    body.body a:hover, body.body a:visited, body.body a:focus {
      outline: none !important;
      box-shadow: none;
      text-decoration: none; }
  body.body .btn:hover,
  body.body .btn:focus {
    outline: 0;
    box-shadow: none !important;
    transition: all 0.3s linear; }
  body.body .btn {
    padding: 8px 15px;
    border-radius: 0;
    color: #ffffff;
    font-size: 16px; }
    body.body .btn:hover {
      box-shadow: none;
      text-decoration: none; }
    body.body .btn:focus {
      box-shadow: none;
      text-decoration: none; }
  body.body .btn-default {
    background: #E1002C !important;
    border: 0; }
